import React, { useState } from 'react';
import { Typography, Button, TextField, FormControlLabel, Checkbox, Link, Grid, Box, Switch } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import Brightness4Icon from '@mui/icons-material/Brightness4';
import Brightness7Icon from '@mui/icons-material/Brightness7';
import { styled } from '@mui/material/styles'; // styled  .
import { Link as RouterLink, useNavigate } from 'react-router-dom'; // react-router-dom Link  
import './App.module.css';
import sign_up from './SignUpPage'

const StyledDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiPaper-root': {
        borderRadius: 20, //   .
        boxShadow: theme.shadows[24], //    .
    },
}));

const IndexPage = () => {
    const navigate = useNavigate();
    const goToSign = () => {
        navigate("/sign_up")
    }
    const goToChat = () => {
        navigate("/chat") //  
    }

    //   darkMode   false .
    const [darkMode, setDarkMode] = useState(false);
    const [open, setOpen] = useState(false);

    //       .
    const darkTheme = createTheme({
        palette: {
            mode: 'dark',
        },
    });

    const lightTheme = createTheme({
        palette: {
            mode: 'light',
        },
    });

    // darkMode true darkTheme , false lightTheme .
    const theme = darkMode ? darkTheme : lightTheme;

    //     .
    const toggleDarkMode = () => {
        setDarkMode(!darkMode);
    };

    //    
    const handleModalOpen = () => {
        setOpen(true);
    };

    const handleModalClose = () => {
        setOpen(false);
    };

    //         
    const handleBackdropClick = (event) => {
        event.stopPropagation();
    };

    return (
        <ThemeProvider theme={theme}>
            <Box
                style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    transform: 'translate(-50%, -50%)'
                }}
            >
                <Typography
                    variant="h6"
                    align="center"
                    gutterBottom
                    style={{
                        color: 'black'
                    }}
                    sx={{
                        fontWeight: 'bold'
                    }}
                >
                        .
                    <td></td>.
                </Typography>
                <Button
                    variant="contained"
                    onClick={handleModalOpen}
                    style={{
                        color: 'black', // Or whichever color you have set
                        width: '150px',            // Set your desired width here.
                        height: '50px',             // Set your desired height/length here.
                        backgroundColor: 'white'
                    }}
                    sx={{
                        m: 1
                    }}
                >
                    
                </Button>
            </Box>
            <StyledDialog
                open={open}
                disableBackdropClick={true}  // Prevent closing the modal by clicking outside of it.
                disableEscapeKeyDown={true}  // Prevent closing the modal by pressing the escape key.
                BackdropProps={{
                    sx: {
                        background: `transparent`, //   
                        opacity: 0.5, //   
                    },
                    onClick: handleBackdropClick,
                }}
                sx={{
                    borderRadius: 500, //     .
                }}
            >
                <DialogContent sx={{ background: 'transparent' }}>
                    <Button onClick={handleModalClose} style={{ position: 'absolute', right: '10px', top: '10px' }}>X</Button>
                    <Typography component="h1" variant="h5">
                        
                    </Typography>
                    <Box component="form" sx={{ mt: 1 }}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="username"
                            label=" "
                            name="username"
                            autoComplete="username"
                            autoFocus
                        />
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label=""
                            type="password"
                            id="password"
                            autoComplete="current-password"
                        />
                    </Box>
                    <FormControlLabel
                        control={<Checkbox value="remember" color="primary" />}
                        label=" "
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                        onClick={goToChat}
                    >
                        
                    </Button> {/*              */}
                    <Grid container>
                        <Grid item xs>
                            <Link href="" variant="body2">
                                 
                            </Link>
                        </Grid>
                        <Grid item>
                            <Button onClick={goToSign}>
                                
                            </Button>
                        </Grid>
                    </Grid>
                    {/*     */}
                    <FormControlLabel
                        className="custom-switch"
                        control={
                            <Switch
                                checked={darkMode}
                                onChange={toggleDarkMode}
                                name="darkModeToggle"
                                color="primary"
                                icon={
                                    <div
                                        sx={{
                                            fontSize: 20,
                                            width: 36,
                                            height: 18,
                                            borderRadius: 10,
                                            background: `linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)`, //  
                                            display: 'flex',
                                            justifyContent: 'center',
                                            alignItems: 'center',
                                        }}
                                    >
                                        <Brightness4Icon sx={{ color: '#000' }} />
                                    </div>
                                }
                                checkedIcon={
                                    <div
                                        sx={{
                                            fontSize: 20,
                                            width: 36,
                                            height: 18,
                                            borderRadius: 10,
                                            background: `linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)`, //  
                                            display: 'flex',
                                            justifyContent: 'center',
                                            alignItems: 'center',
                                        }}
                                    >
                                        <Brightness7Icon sx={{ color: '#fff' }} />
                                    </div>
                                }
                            />
                        }
                        label={darkMode ? '  ' : '  '}
                        sx = {{ width: 'auto' }}
                    />
                </DialogContent>
            </StyledDialog>
        </ThemeProvider>
    );
};

export default IndexPage;
