import React, { useState } from 'react';
import { Typography, Button, TextField, Link, Grid, Paper } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import './App.module.css';
import { Link as RouterLink, useNavigate } from 'react-router-dom';

const theme = createTheme();

const SignUpPage = () => {
    const navigate = useNavigate();
    const goToLogin = () => {
        navigate("/");
    }

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const handleSignUp = () => {
        //     .
    };

    return (
        <ThemeProvider theme={theme}>
            <Grid container justifyContent="center" alignItems="center" className="container">
                <Paper elevation={3} className="paper" sx={{ width: '400px', padding: 10, margin: 20, borderRadius: 10}}>
                    <Typography component="h1" variant="h5">
                        <b></b>
                    </Typography>
                    <form onSubmit={handleSignUp}>
                        <TextField
                            label=""
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            value={name}
                            onChange={(e) => setName(e.target.value)}
                            required
                        />
                        <TextField
                            label=""
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            value={email}
                            onChange={(e) => setEmail(e.target.value)}
                            required
                            type="email"
                        />
                        <TextField
                            label=""
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            required
                            type="password"
                        />
                        <Button type="submit" variant="contained" color="primary" margin="normal" fullWidth>
                            
                        </Button>
                        <Grid container justifyContent="flex-end">
                            <Grid item>
                                <Button onClick={goToLogin}>
                                      ? 
                                </Button>
                            </Grid>
                        </Grid>
                    </form>
                </Paper>
            </Grid>
        </ThemeProvider>
    );
};

export default SignUpPage;
